<template>
    <div class="screen">
        <transition name="operation-fade">
             <div v-show="!show" class="operation screen" id="operation">
                 <header class="Welcome">
                     <p>欢迎体验</p>
                     <p>vue-image-cutting</p>
                 </header>
                 <article class="content">
                     <div class="Entrance">
                         选择一个本地图片
                         <input type="file" @change="shangchuan($event)">
                     </div>
                     <div class="Entrance" @click="start">
                         直接体验
                     </div>
                     <div class="Description">
                         <p>文档地址</p>
                         <hr>
                         <p><a href="https://www.npmjs.com/package/vue-image-cutting"> https://www.npmjs.com/package/vue-image-cutting</a></p>
                         <hr>
                         <p>感谢问题的反馈（邮箱）</p>
                         <hr>
                         <p>1033279566@qq.com</p>
                     </div>
                 </article>
             </div>
        </transition>
        <transition name="assembly-fade">
             <div v-show="show" class="assembly screen" id="assembly">
               <div class="assembly-topList">
                   <transition name="t2">
                        <div v-show="!imageListShow" class="image-listDrop-down" @click="dropDown" style="position: absolute;
    width: 100%;">
                            <img src="../assets/gengduo.png" alt="" style="transform: rotate(90deg);">
                        </div>
                   </transition>
                   <transition name="t1">
                     <div v-show="imageListShow">
                         <div class="image-list">
                             <img @click="imageListClick(item.url)" v-for="(item,index) in imageList" :key="index" :src="item.url" alt="">
                         </div>
                         <div class="image-listDrop-down" @click="dropDown">
                             <img src="../assets/gengduo.png" alt="" style="transform: rotate(-90deg);">
                         </div>
                     </div>
                   </transition>
               </div>
                 <Vue-imageCutting
                         :picturePath="picturePathData"
                         :buttonColor="buttonColorData"
                         :backgroundColor="backgroundColorData"
                         :iconColor="iconColorData"
                         :format="formatData"
                         :crossDomain="crossDomainData"
                         :maskOpacity="maskOpacityData"
                         :minZoom="minZoomData"
                         :maxZoom="maxZoomData"
                         @confirm="confirmMethod"
                         @cancel="cancelMethod"
                         @rotate="rotateMethod"
                         @restore="restoreMethod"
                         @loading="loadingMethod"
                         @touching="touchingMethod"
                 />
                 <transition name="PopUps-fade">
                     <div class="PopUps" v-show="PopUpsShow">
                        <div class="PopUps-tab">
                            <div class="PopUps-tab-list" v-for="(item,index) in PopUpsTabList" :key="index" @click="optionTab(index)" :style="indexShow == index?'background: rgba(14,154,0,0.6)':''">{{item.name}}</div>
                        </div>
                        <div class="PopUps-content">
                            <div class="PopUps-content-img" v-show="indexShow == 0">
                                <img :src="base64" alt="">
                            </div>
                            <!--<div class="PopUps-content-str" v-show="indexShow == 1">-->
                                <!--{{base64}}-->
                            <!--</div>-->
                        </div>
                        <div class="PopUps-btn">
                            <div class="btn" @click="PopUpsBtn(1)">返回</div>
                            <div class="btn" @click="PopUpsBtn(2)">关闭</div>
                        </div>
                     </div>
                 </transition>
             </div>
        </transition>
    </div>
</template>
<script>
    export default {
        data () {
            return {
                imageListShow:false,
                PopUpsShow:false,
                indexShow:0,
                show:false,
                base64:"",
                rotate:null,
                imageList:[
                    {url:require("../assets/timg.jpg")},
                    {url:require("../assets/timg (1).jpg")},
                    {url:require("../assets/timg (2).jpg")},
                    {url:require("../assets/timg (3).jpg")},
                    {url:require("../assets/timg (4).jpg")},
                    {url:require("../assets/timg (5).jpg")},
                    {url:require("../assets/timg (6).jpg")},
                ],
                PopUpsTabList:[
                    {name:"image",show:true},
                    // {name:"Base64",show:false}
                ],
                list:[
                    "http://image2.xyzs.com/upload/63/d2/1438218220844704/20150731/143835748885276_0.jpg",
                    "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1600515104156&di=260283252a214362167aff4caa472f14&imgtype=0&src=http%3A%2F%2Fyouimg1.c-ctrip.com%2Ftarget%2Ffd%2Ftg%2Fg6%2FM04%2F22%2FA9%2FCggYs1dAJtOAYzGmAAVZL3pe9bg758_R_1024_10000_Q90.jpg",
                    "https://ss0.bdstatic.com/70cFuHSh_Q1YnxGkpoWK1HF6hhy/it/u=3130395866,2625106020&fm=26&gp=0.jpg",
                    "https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1600626987114&di=be76a575f3aee1dd336afcf4e6f7390c&imgtype=0&src=http%3A%2F%2Fclubimg.club.vmall.com%2Fdata%2Fattachment%2Fforum%2F201903%2F25%2F171556cp3hezcddoynclht.jpg",
                    "https://ss1.bdstatic.com/70cFuXSh_Q1YnxGkpoWK1HF6hhy/it/u=1799309166,1160915813&fm=26&gp=0.jpg",
                    "https://myd.tenqent.com/uploads/20200801/2fc4b7322634b774eee9a5017160bd7d.jpeg"
                ],
                maskOpacityData:0.7,
                formatData:"image/png",
                minZoomData:0.5,
                crossDomainData:false,
                maxZoomData:3,
                iconColorData:"#ffffff",
                backgroundColorData:'#000000',
                buttonColorData:'#0e9a00',
                picturePathData:""
            }
        },
        methods:{
            dropDown(){
                this.imageListShow = !this.imageListShow
            },
            imageListClick(url){
                this.picturePathData = url
            },
            PopUpsBtn(num){
                if(num == 1){
                    this.PopUpsShow = false;
                    this.show = false;
                }else{
                    this.PopUpsShow = false;
                }
            },
            optionTab(index){
                this.indexShow = index;
            },
            start(){
                this.picturePathData = require("../assets/timg (7).jpg");
                this.show = true;
            },
            shangchuan($event){
                    if($event){
                        this.picturePathData = $event.target.files[0];
                    }
                    // var reader = new FileReader();
                    // reader.readAsDataURL($event.target.files[0]);
                    // reader.onloadend=(e)=>{
                    //     this.picturePathData = e.target.result
                    // };
            },
            touchingMethod(){
                // console.log(event)
            },
            restoreMethod(event){
                // console.log(event);
                this.rotate = event.angle;
            },
            rotateMethod(event){
                this.rotate = event.angle;
                // console.log(event);
            },
            cancelMethod(){
                this.show = false;
            },
            loadingMethod(event){
                if(!event.LoadingStatus){
                    console.log("图片加载失败");
                }else{
                    this.show = true;
                    console.log("图片加载成功");
                }
                // console.log(event);
            },
            confirmMethod(event){
                this.base64 = event.base64;
                this.PopUpsShow = true;
                // console.log(event);
            }
        }
    }
</script>

<style scoped>
    .t2-enter-active {
        transition: all 2.5s;
    }
    .t2-enter, .t2-leave-to
        /* .slide-fade-leave-active 用于 2.1.8 以下版本 */ {
        opacity: 0;
    }
    .t1-enter-active {
        transition: all 0.5s ease;
    }
    .t1-leave-active {
        transition: all 0.5s ;
    }
    .t1-enter, .t1-leave-to
        /* .slide-fade-leave-active 用于 2.1.8 以下版本 */ {
        transform: translateY(-100px);
        opacity: 0;
    }
    .PopUps-fade-enter-active, .PopUps-fade-leave-active {
        transition: opacity 0.7s
    }
    .PopUps-fade-enter, .PopUps-fade-leave-to /* .fade-leave-active, 2.1.8 版本以下 */ {
        opacity: 0
    }
    .operation-fade-enter-active, .operation-fade-leave-active {
        transition: opacity 0.7s
    }
    .operation-fade-enter, .operation-fade-leave-to /* .fade-leave-active, 2.1.8 版本以下 */ {
        opacity: 0
    }
    .assembly-fade-enter-active, .assembly-fade-leave-active {
        transition: opacity 0.7s
    }
    .assembly-fade-enter, .assembly-fade-leave-to /* .fade-leave-active, 2.1.8 版本以下 */ {
        opacity: 0
    }
    .assembly-topList{
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        /*overflow: scroll;*/
        width: 100%;
        height: 125px;
    }
    .image-listDrop-down{
        text-align: center;
        color: white;
        height: 25px;
        /*background: rgba(14, 154, 0,0.3);*/
    }
    .image-listDrop-down img{
        width: auto;
        height: 100%;
    }
    .image-list{
        width: 100%;
        height: 100px;
        display: flex;
        overflow: scroll;
    }
    .image-list img{
        width: auto;
        height: 100%;
    }
    .PopUps-content-str{
        word-wrap:break-word;
        padding: 0 10px;
        height: 100%;
        color: white;
        width: calc(100% - 20px);
    }
    .PopUps-content-img{
        height: calc(100% - 20px);
        width: calc(100% - 20px);
        padding: 10px;
    }
    .PopUps-content-img img{
        height: auto;
        width: calc(100% - 20px);
        position: absolute;
        top: 20%;
    }
    .PopUps-tab-list{
        /*width: 50%;*/
        flex: 1;
        height: 100%;
        font-size: 18px;
        display: flex;
        justify-content : center;
        align-items: center;
        color: white;
    }
    .PopUps-tab{
        width: 100%;
        height: 10%;
        display: flex;

    }
    .PopUps-content{
        width: 100%;
        height: 80%;

    }
    .PopUps-btn .btn{
        width: 30%;
        height: 60%;
        /* border: 1px solid red; */
        display: flex;
        align-items: center;
        justify-content: center;
        color: white;
        background: rgba(14, 154, 0, 0.6);
        border-radius: 5px;
    }
    .PopUps-btn{
        width: 100%;
        height: 10%;
        display: flex;
        align-items: center;
        justify-content: space-around;
    }
    .PopUps{
        width: 80%;
        height: 70%;
        background:rgba(26,26,26,0.9);
        position: absolute;
        left: 10%;
        top: 15%;
        border-radius: 5px;
        overflow: hidden;
        border: 1px solid rgba(14, 154, 0, 0.4);
    }
    .Description{
        color: white;
        font-size: 13px;
        margin-top: 5%;
        background: rgba(66,67,64,0.5);
    }
    .Description p{
        padding: 5px 5px;
    }
    .Description a{
        color: white;
    }
    .content{
        width: calc(100% - 20%);
        height: calc(60% - 20%);
        padding: 16% 10%;
    }
    .content .Entrance input[type=file]{
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        opacity: 0;
    }
    .content .Entrance{
        width: 100%;
        height: 15%;
        border: 2px solid #3F88CB;
        margin-top: 7%;
        background: rgba(27,88,169,0.5);
        border-radius: 20px;
        font-size: 19px;
        color: white;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
    }
    .Welcome{
        width: 100%;
        height: 40%;
        /* text-align: center; */
        display: flex;
        align-items: center;
        font-size: 25px;
        font-weight: bolder;
        justify-content: center;
        color: #1D5FB4;
        white-space:nowrap;
    }
    .operation{
        background-image: url('../assets/timg.jpg');
        background-size: 100% 100%;
    }
    .assembly{

    }
    .screen{
        width: 100%;
        height: 100%;
    }
</style>